.single-post-wrapper {

    .way {
        font-size: 14px;
        color: #787993;
    }

    .subtitle {
        padding: 20px 0;

        h1 {
            font-family: 'Gilroy normal', sans-serif;
            font-size: 34px;
            line-height: 41px;
            color: #414D6A;
        }
    }

    .about-post {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        .left {
            width: 620px;
            display: flex;

            .little-images {
                margin-right: 20px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;

                img {
                    width: 85px;
                    height: 85px;
                }
            }

            .main-image {
                img {
                    width: 500px;
                    height: 480px;
                }
            }
        }

        .right {
            width: calc(1144px - 620px);

            .id-post {
                width: 100%;
                display: flex;
                justify-content: space-between;

                span {
                    font-size: 14px;
                    color: #414D6A;
                }
            }

            .price-post {
                font-size: 40px;
                letter-spacing: -0.00016833px;
                color: #444444;
                margin: 10px 0;
            }

            .location {
                font-size: 14px;
                letter-spacing: -5.89154e-05px;
                color: #4E72CA;
            }

            .number-wrapper {
                display: flex;
                justify-content: flex-start;
                margin-top: 25px;

                .wrapper {
                    display: flex;
                    background: #65E99F;
                    border-radius: 19px;

                    .left-wrapper {
                        display: flex;
                        align-items: center;
                        padding: 9px 23px;

                        p {
                            font-family: 'Gilroy bold', sans-serif;
                            font-weight: bold;
                            font-size: 16px;
                            color: #000000;
                        }

                        input {
                            width: 50px;
                            background: transparent;
                            outline: none;
                            border: none;
                            margin-left: 10px;
                            font-family: 'Gilroy bold', sans-serif;
                            font-weight: bold;
                            font-size: 16px;
                            color: #000000;
                        }
                    }

                    .right-wrapper {
                        .show {
                            padding: 10px 20px;
                            font-size: 15px;
                            color: #000000;
                            background: #41D481;
                            border-radius: 19px;
                            border: none;
                            outline: none;
                        }
                    }
                }
            }

            .author-post {
                margin: 10px 0;
                font-size: 14px;
                color: #000000;

                a {
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                    font-size: 14px;
                    color: #2AAE63;
                }
            }

            .button-group {
                display: flex;
                padding: 20px 0;

                .button {
                    text-decoration: none;
                    padding: 15px 28px;
                    background: #304FFF;
                    border-radius: 23.5px;
                    font-family: 'Gilroy bold', sans-serif;
                    font-size: 14px;
                    color: #FFFFFF;

                    &:first-of-type {
                        margin-right: 20px;
                    }
                }
            }

            .table {
                font-family: 'Gilroy normal', sans-serif;

                tr {
                    display: flex;

                    .top {
                        border-top: none;
                    }

                    .right {
                        border-right: none;
                    }

                    .bottom {
                        border-bottom: none;
                    }

                    td {
                        padding: 14px;
                        width: 33.33%;
                        display: flex;
                        border: solid #787993 0.5px;
                        flex-direction: column;

                        span {
                            font-size: 14px;
                            margin-bottom: 5px;
                            color: #444444;
                        }

                        a {
                            font-size: 18px;
                            color: #4E72CA;
                        }
                    }

                    .last {
                        width: 100%;
                    }
                }
            }

            .overwiew {
                font-family: 'Gilroy bold', sans-serif;
                font-weight: 500;
                font-size: 17px;
                line-height: 25px;
                color: #414D6A;

                span {
                    margin: 15px 0;
                    display: inline-block;
                }
            }
        }
    }
}

.extra-post {
    .post-type {
        margin-top: 30px;

        .top {
            .post-status {
                font-family: 'Gilroy bold', sans-serif;
                font-weight: bold;
                font-size: 24px;
                color: #9B9B9B;
                background: transparent;
                border: none;
                outline: none;
                transition: all ease 0.5s;

                &:first-of-type,
                &:nth-of-type(2) {
                    margin-right: 77px;
                }

                &.active {
                    color: #4E72CA;
                }
            }
        }

        .bottom {
            margin-top: 12px;
            position: relative;

            .line {
                width: 100%;
                height: 1px;
                background: #D5D5D5;
            }

            .active-new {
                position: absolute;
                left: 50px;
                top: 0;
                bottom: 0;
                margin: auto 0;
                width: 130px;
                height: 3px;
                transition: all ease 0.5s;
                background: transparent;

                &.active {
                    background: #4E72CA;
                }
            }

            .active-best {
                position: absolute;
                left: 350px;
                top: 0;
                bottom: 0;
                margin: auto 0;
                width: 150px;
                height: 3px;
                transition: all ease 0.5s;
                background: transparent;

                &.active {
                    background: #4E72CA;
                }
            }
        }
    }
    .article{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .subtitle{
            width: 100%;
        }
    }
}